<template>
  <div class="login-wrap">
    <div class="ms-register">
      <div class="ms-title">注册一下叭！</div>
      <el-form :model="registerForm" ref="register" label-width="0px" class="ms-content">
        <el-form-item prop="name" class="el_form_item">
          <el-input v-model="registerForm.name" placeholder="名字" class="input"></el-input>
        </el-form-item>
        <el-form-item prop="mail" class="el_form_item">
          <el-input v-model="registerForm.mail" placeholder="邮箱" class="input email"></el-input>
        </el-form-item>
        <el-form-item prop="password" class="el_form_item">
          <el-input v-model="registerForm.password" placeholder="密码" class="input"></el-input>
        </el-form-item>
        <el-form-item prop="password_copy" class="el_form_item">
          <el-input v-model="registerForm.password_copy" placeholder="再次输入密码" class="input"></el-input>
        </el-form-item>
       
        <el-form-item prop="password">
          <el-input
            type="password"
            placeholder="验证码"
            v-model="registerForm.password"
            @keyup.enter.native="getVerifyCode()"
          >
            <el-button slot="prepend" icon="el-icon-lx-lock"  @click="getVerifyCode()">获取验证码</el-button>
          </el-input>
        </el-form-item>

        <div class="login-btn">
          <el-button type="primary" @click="register()">注册</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  data: function () {
    return {
      registerForm: {
        phone: '',
        password: '',
        password_copy: '',
        mail: '',
        name: '',
        workplace: '',
        address: '',
        type: ''
      }
    }
  },
  methods: {
    ...mapMutations(['changeLogin']),
    register () {
      let _this = this
      this.tnApi.userApi.register(_this.registerForm)
        .then(res => {
          if (res.status === 200) {
            console.log(res)
            this.$router.push('/login')
          }
        })
        .catch(error => {
          console.log(error)
          alert('注册失败')
        })
    },
    
    getVerifyCode () {
      let _this = this
      this.tnApi.userApi.getVerifyCode(_this.registerForm.phone)
    }
  }
}
</script>

<style scoped>
  .login-wrap {
    position: relative;
    width: 100%;
    height: 1000px;
    background-image: url(../assets/img/whole_bg.jpg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
  }
  .ms-title {
    width: 100%;
    line-height: 66px;
    text-align: center;
    font-size: 30px;
    color: #fff;
    border-bottom: 1px solid #ddd;
  }
  .ms-register {
    position: absolute;
    left: 15%;
    right: 15%;
    top: 20%;
    bottom:20%;
    width: 70%;
    margin:auto;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.3);
    text-align: center;
    /* overflow: scroll; */
  }
  .ms-content {
    padding: 30px 30px;
  }
  .el_form_item {
    margin-bottom: 20px;
    /* text-align: center; */
  }
  .login-btn {
    text-align: center;
  }
  .login-btn button {
    width: 20%;
    height: 36px;
    margin-bottom: 10px;
  }
</style>



